    font-family: 'Rubik', sans-serif;
}

.dvl-container {
    width: 60%;
    margin: 100px auto;
}

.dvl-color-bg-loader {
    background-color: #f7f4f3 !important;
}

.dvl-color-loader {
    color: black !important;
}

@media only screen and (max-width: 1281px) {
    .dvl-container {
        width: 80%;
    }
}

/* @media only screen and (max-width: 1025px) {

} */

@media only screen and (max-width: 961px) {
