    }
}
.deliveryHeader {
    width: 100%;
    height: 100px;
    background-color: white;
    box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
}

.deliveryHeader .deliveryHeader-topbar,
.deliveryHeader .deliveryHeader-bottombar {
    width: 60%;
    height: 50px;
    margin: 0 auto;
}

.deliveryHeader .deliveryHeader-topbar ul,
.deliveryHeader .deliveryHeader-bottombar ul {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    list-style: none;
    height: 100%;
}

.deliveryHeader .deliveryHeader-topbar ul li,
.deliveryHeader .deliveryHeader-bottombar ul li {
    padding: 10px;
}

.deliveryHeader .deliveryHeader-topbar ul li .dvl-search-container {
    background-color: #f7f4f3;
    border-radius: 20px;
    padding: 5px;
    border: 1px solid #868686;
}

.deliveryHeader .deliveryHeader-topbar ul li .dvl-search-container input {
    background-color: #f7f4f3;
    margin-right: 5px;
    height: 30px;
    padding: 10px 24px 10px 24px;
    border-right: 1px solid #868686;
    border-top: none;
    border-left: none;
    border-bottom: none;
    outline: none;
    color: #868686;
    font-weight: bold;
}

.deliveryHeader .deliveryHeader-topbar ul li .dvl-search-container i {
    font-size: 18px;
    color: #FB9300;
}

.deliveryHeader .deliveryHeader-topbar ul li i {
    color: #FB9300;
}

.deliveryHeader .deliveryHeader-topbar ul li a {
    color: #868686;
}

.deliveryHeader .deliveryHeader-topbar ul li button {
    border-radius: 20px;
    background-color: #FB9300;
    color: white;
    font-size: 12px;
    font-weight: bold;
    height: 30px;
    padding: 10px 24px 10px 24px;
    outline: none;
    border: none;
    cursor: pointer;
    transition: 0.5s linear;
}

.deliveryHeader .deliveryHeader-topbar ul li button:hover {
    background-color: rgb(145, 53, 20);
}

.deliveryHeader .deliveryHeader-bottombar ul li a {
    color: black;
    font-weight: bold;
    font-size: 16px;
}

.deliveryHeader .deliveryHeader-bottombar ul li button {
    border-radius: 20px;
    background-color: olivedrab;
    color: white;
    font-size: 18px;
    font-weight: bold;
    height: 40px;
    padding: 10px 24px 10px 24px;
    outline: none;
    border: none;
    cursor: pointer;
    transition: 0.5s linear;
}

.deliveryHeader .deliveryHeader-bottombar ul li button:hover {
    background-color: rgb(67, 87, 27);
}

/*hamburger*/

.deliveryHeader .button_container {
    position: absolute;
    top: 25px;
    right: 50px;
    height: 27px;
    width: 35px;
    cursor: pointer;
    z-index: 999999;
    transition: opacity .25s ease;
    display: none;
}

.deliveryHeader .button_container:hover {
    opacity: .7;
}

.deliveryHeader .button_container.active .top {
    -webkit-transform: translateY(11px) translateX(0) rotate(45deg);
            transform: translateY(11px) translateX(0) rotate(45deg);
    background: #FFF;
}

.deliveryHeader .button_container.active .middle {
    opacity: 0;
    background: #FFF;
}

.deliveryHeader .button_container.active .bottom {
    -webkit-transform: translateY(-11px) translateX(0) rotate(-45deg);
            transform: translateY(-11px) translateX(0) rotate(-45deg);
    background: #FFF;
}

.deliveryHeader .button_container span {
    background: #FB9300;
    border: none;
    height: 5px;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: all .35s ease;
    cursor: pointer;
}
  
.deliveryHeader .button_container span:nth-of-type(2) {
    top: 11px;
    width: 20px;
}
  
.deliveryHeader .button_container span:nth-of-type(3) {
    top: 22px;
}

/*overflow-mobile-menu*/

.overlay {
    position: fixed;
    background: #444;
    top: 0;
    right: 0;
    width: 0%;
    height: 100%;
    opacity: 0;
    visibility: hidden;
    transition: opacity .35s, visibility .35s, width .35s;
    overflow: hidden;
    z-index: 99999;
    box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
}

.overlay.open {
    opacity: 1;
    visibility: visible;
    height: 100%;
    width: 90%;
}

.overlay .overlay-menu {
    margin-top: 100px;
}

.overlay .overlay-menu ul {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-left: 0;
}

.overlay .overlay-menu .ul-top-moblie {
    border-radius: 5px;
    width: 90%;
    margin: 0 auto;
    box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
}

.overlay .overlay-menu .ul-top-moblie li a {
    color: white;
}

.overlay .overlay-menu .ul-top-moblie li i {
    color: white;
}

.overlay .overlay-menu ul li {
    padding: 10px;
    position: relative;
    opacity: 0;
}

.overlay .overlay-menu ul li button {
    border-radius: 5px;
    background-color: #FB9300;
    height: 40px;
    border: none;
    margin-right: 10px;
    margin-left: 10px;
    color: white;
    font-weight: 800;
    cursor: pointer;
}

.overlay .overlay-menu ul li button:hover {
    background-color: #965e11;
}

.overlay .overlay-menu ul li input {
    height: 40px;
    padding: 10px;
    border: 1px solid #868686;
    border-radius: 5px;
    outline: none;
}

.overlay .overlay-menu .ul-bottom-moblie {
    flex-direction: column;
}

.overlay .overlay-menu .ul-bottom-moblie li {
    width: 90%;
    height: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.5s linear;
    margin-top: 5px;
    margin-bottom: 5px;
    box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
}

.overlay .overlay-menu .ul-bottom-moblie li a {
    color: #FB9300;
    font-size: 16px;
}

.overlay .overlay-menu .ul-bottom-moblie li a:hover {
    color: #88540b;
}

/*anim*/

.overlay.open .overlay-menu ul li {
    -webkit-animation: fadeInRight .5s ease forwards;
            animation: fadeInRight .5s ease forwards;
    -webkit-animation-delay: .35s;
            animation-delay: .35s;
}

.overlay.open .overlay-menu ul li:nth-of-type(2) {
    -webkit-animation-delay: .4s;
            animation-delay: .4s;
}

.overlay.open .overlay-menu ul li:nth-of-type(3) {
    -webkit-animation-delay: .45s;
            animation-delay: .45s;
}

.overlay.open .overlay-menu ul li:nth-of-type(4) {
    -webkit-animation-delay: .50s;
            animation-delay: .50s;
}

.overlay.open .overlay-menu ul li:nth-of-type(5) {
    -webkit-animation-delay: .55s;
            animation-delay: .55s;
}

.overlay.open .overlay-menu ul li:nth-of-type(5) {
    -webkit-animation-delay: .60s;
            animation-delay: .60s;
}

@-webkit-keyframes fadeInRight {
    0% {
        opacity: 0;
        left: 20%;
    }
    100% {
        opacity: 1;
        left: 0;
    }
}

@keyframes fadeInRight {
    0% {
        opacity: 0;
        left: 20%;
    }
    100% {
        opacity: 1;
        left: 0;
    }
}

/*anim-end*/

